<template>
<section class="center-oldPicture my-3">
  <div class="container mx-auto p-1 sm:mx-1 max-w-screen-md  md:mx-1 max-w-screen-lg lg:mx-1 max-w-screen-xl xl:mx-1 max-w-screen-2xl">
    <div class="w-full h-auto bg-white border border-slate-300 rounded-sm p-2">
      <div class="flex flex-row align-center justify-between">
        <div>
          <slot name="subject"><p class="font-bold text-start text-black text-sm sm:text-xs"></p></slot>
        </div>
        <div class="flex flex-row">
          <div><span class="font-bold text-start text-black text-sm sm:text-xs"></span></div>
          <div><font-awesome-icon :icon="['fas','angle-left']" class="text-slate-800 text-sm font-semibold"></font-awesome-icon></div>
        </div>
      </div>
        <div class="bg-slate-800 h-0.5 w-full my-1"></div>
        <div class="flex flex-row align-center justify-between w-12/12 sm:w-full">
          <Suspense>
            <template #default>
              <NostalogyBase :list="list"/>
            </template>
            <template #fallback>
              <LoaderComponentTwo/>
            </template>
          </Suspense>

        </div>
    </div>
  </div>
</section>
</template>

<script>
import{onMounted,reactive} from "vue";
import{defineAsyncComponent} from "vue";
import {FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
import EndPoints from "@/service/EndPoints";
import {handleError} from "@/components/composable/handleError";
const LoaderComponentTwo=defineAsyncComponent(()=>import('@/components/composable/LoaderComponentTwo'))
const NostalogyBase=defineAsyncComponent(()=>import('@/components/main/worldCup/NostalogyBase'))
export default {
  name: "OldPicture",
  components: {NostalogyBase, LoaderComponentTwo, FontAwesomeIcon},
  setup(){
    const list=reactive([])
    const result=onMounted(()=>{
      EndPoints.getTractor()
      .then((response)=>{
        if(response.data.success){
          list.push(response.data.data[3])
        }
      })
      .catch((error)=>{
      handleError(error)
      })
    })
    return{
      list,
      result,

    }
  }
}
</script>

<style scoped>
.fade-enter-active,
.fade-leave-active{
  transition: all ease-in-out 0.5s;
}
.fade-enter-from,
.fade-leave-to{
  opacity: 0;
}
.text{
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: normal;
}
</style>