<template>
<section class="main-esteghlal">
  <div class="container mx-auto p-1 sm:mx-1 max-w-screen-md md:mx-1 max-w-screen-lg lg:mx-1 max-w-screen-xl xl:mx-1 max-w-screen-2xl">
    <div class="w-full h-auto">
      <div class="w-full bg-white p-1 rounded-md border border-1 border-slate-300" >
<!--        */style:height:350px/*-->
        <div class="w-full flex flex-row align-center justify-between">
          <div>
            <slot name="title">
              <h2 class="font-bold text-black text-start font-md"></h2>
            </slot>
          </div>
          <div class="flex flex-row">
            <p class="font-semibold text-sm font-slate-600"></p>
            <font-awesome-icon :icon="['fas','angle-left']" class="font-semibold text-sm text-slate-800"></font-awesome-icon>
          </div>
        </div>
        <div class="w-full h-0.5 bg-slate-600 my-1"></div>
        <div class="flex flex-row items-center justify-between w-full lg:flex lg:flex-row lg:items-center lg:justify-between lg:w-full lg:h-auto md:flex md:flex-row md:justify-between md:w-full sm:grid sm:grid-cols-1 sm:grid-rows-3 sm:gap-2 sm:mx-auto sm:w-10/12 p-1" >
          <div class="w-3/12 lg:w-3/12 md:w-3/12 sm:w-full p-1">
            <Suspense>
              <template #default>
                <main>
                  <div class="grid grid-cols-2 grid-rows-2 gap-1">
                    <DefinePerspolise counter="1" :menu="list"/>
                    <DefinePerspolise counter="2" :menu="list"/>
                    <DefinePerspolise counter="3" :menu="list"/>
                    <DefinePerspolise counter="4" :menu="list"/>
                  </div>
                </main>
              </template>
              <template #fallback>
                <LoaderComponentTwo/>
              </template>
            </Suspense>
          </div>
          <div class="w-5/12 lg:w-5/12 md:w-5/12 sm:w-full p-1">
            <div class="grid grid-cols-1 grid-rows-1 w-full h-full">
              <div v-for="value in list" :key="value">
              <DefinePerspolise counter="0" :menu="list">
                  <template #myImage>
                    <img @error="notShow" :src="`${value.news[0].news_image[0].url}`" alt="" class="w-full" style="height: 260px">
                  </template>
                <template #text>
                  <p class="font-bold text-sm sm:text-sm">{{value.news[0].title}}</p>
                </template>
              </DefinePerspolise>
              </div>
            </div>
          </div>
          <div class="w-3/12 lg:w-3/12 md:w-3/12 sm:w-full p-1">
            <Suspense>
              <template #default>
                <main>
                  <div class="grid grid-cols-2 grid-rows-2 gap-1">
                    <DefinePerspolise counter="5" :menu="list"/>
                    <DefinePerspolise counter="6" :menu="list"/>
                    <DefinePerspolise counter="7" :menu="list"/>
                    <DefinePerspolise counter="8" :menu="list"/>
                  </div>
                </main>
              </template>
              <template #fallback>
                <LoaderComponentTwo/>
              </template>
            </Suspense>
          </div>
        </div>
      </div>
    </div>
  </div>
</section>


</template>

<script>
import {reactive,onMounted,ref} from "vue";
import {defineAsyncComponent} from "vue";
import EndPoints from "@/service/EndPoints";
import {handleError} from "@/components/composable/handleError";
import {FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
const DefinePerspolise=defineAsyncComponent(()=>import('@/components/main/layout/DefinePerspolise'))
const LoaderComponentTwo=defineAsyncComponent(()=>import('@/components/composable/LoaderComponentTwo'))
export default {
  name: "MainEsteglal",
  components: {LoaderComponentTwo, DefinePerspolise, FontAwesomeIcon},
  setup(){
    const showSlider=ref(false)
    const list=reactive([])
    const result=onMounted(async ()=>{
     await EndPoints.getPerspolise()
      .then((response)=>{
        if(response.data.success){
          list.push(response.data.data[1])
        }
      })
      .catch((error)=>{
        handleError(error)
      })
    })
    const notFound=ref('https://footballi.net/assets/shared-application/images/placeholders/ic_default_news_image.png')
    function notShow(event){
      event.target.src=notFound.value
    }
    return{
       notShow,notFound,
      showSlider,
      list,
      result,
    }
  }
}
</script>

<style scoped>
.title{
  overflow:hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>