<template>
<section class="center-tractor">
  <div class="container mx-auto p-1 sm:mx-1 max-w-screen-md md:mx-1 max-w-screen-lg lg:mx-1 max-w-screen-xl xl:mx-1 max-w-screen-2xl">
    <div class="w-full h-auto">
      <div class="flex flex-row align-center justify-around lg:flex lg:flex-row lg:items-center lg:justify-around lg:w-full sm:grid md:flex md:flex-row md:w-full md:justify-between sm:grid-cols-1 sm:grid-rows-2 sm:gap-2 sm:w-10/12 sm:mx-auto">
        <div class="w-6/12 lg:w-6/12 md:w-6/12 sm:w-full h-auto">
          <div class="bg-white border border-1 border-slate-300 rounded-sm p-1">
            <div class="flex flex-row align-center justify-between">
              <div><p class="font-bold text-sm text-start text-black"></p></div>
              <div class="flex flex-row">
                <span class="font-semibold text-slate-700 text-sm"></span>
                <font-awesome-icon :icon="['fas','angle-left']" class="text-sm text-slate-700 font-semibold"></font-awesome-icon>
              </div>
            </div>
            <div class="bg-slate-700 h-0.5 my-1 w-full"></div>
            <Suspense>
              <template #default>
                <HomeTractor :list="list"/>
              </template>
              <template #fallback>
                <LoaderComponentTwo/>
              </template>
            </Suspense>
          </div>
        </div>
        <div class="w-5/12 lg:w-5/12 md:w-5/12 sm:w-full" style="{height: 384px}">
          <Suspense>
            <template #default>
              <NewsPapers/>
            </template>
            <template #fallback>
              <LoaderComponentTwo/>
            </template>
          </Suspense>
        </div>
      </div>
    </div>
  </div>
</section>
</template>

<script>
import {FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
import{defineAsyncComponent} from "vue";
import{onMounted,reactive,ref} from 'vue'
import EndPoints from "@/service/EndPoints";
import {handleError} from "@/components/composable/handleError";
const LoaderComponentTwo=defineAsyncComponent(()=>import('@/components/composable/LoaderComponentTwo'))
const HomeTractor=defineAsyncComponent(()=>import('@/components/main/worldCup/HomeTractor'))
const NewsPapers=defineAsyncComponent(()=>import('@/components/NewsPapers'));
export default {
  name: "TractorBase",
  components: {HomeTractor, LoaderComponentTwo, NewsPapers, FontAwesomeIcon,},
  setup(){
   const notFound=ref('https://footballi.net/assets/shared-application/images/placeholders/ic_default_news_image.png')
    function notShow(event){
     event.target.src=notFound.value
    }
    const list=reactive([]);
    const result=onMounted(async ()=>{
      await EndPoints.getTractor()
      .then((response)=>{
        if(response.data.success){
          list.push(response.data.data[2])
        }
      })
      .catch((error)=>{
        handleError(error)
      })
    })

    return{
      list,
      result,
      notFound,
      notShow,
    }
  }
}
</script>

<style scoped>
.swiper{
  height: 320px;
}
.swiper-button-next{
  size: 13px!important;
  background-color: lightgrey;
  border-radius: 50%;
  padding-left: 5px;
}
</style>