<template>
  <section class="center-oldPicture my-3">
    <div class="container mx-auto p-1 sm:mx-1 max-w-screen-md md:mx-1 max-w-screen-lg lg:mx-1 max-w-screen-xl xl:mx-1 max-w-screen-2xl">
      <div class="w-full h-auto bg-white border border-slate-300 rounded-sm p-2">
        <div class="flex flex-row align-center justify-between">
          <div>
            <p class="font-bold text-start text-black text-sm sm:text-xs"> </p>
          </div>
          <div class="flex flex-row">
            <div><span class="font-bold text-start text-black text-sm sm:text-xs"></span></div>
            <div><font-awesome-icon :icon="['fas','angle-left']" class="text-slate-800 text-sm font-semibold"></font-awesome-icon></div>
          </div>
        </div>
        <div class="bg-slate-800 h-0.5 w-full my-1"></div>
        <div class="flex flex-row align-center justify-between w-full sm:w-full">
          <Suspense>
            <template #default>
              <ReportImage :list="list"/>
            </template>
            <template #fallback>
              <LoaderComponentTwo/>
            </template>
          </Suspense>

        </div>
      </div>
    </div>
  </section>
</template>

<script>
import{reactive,onMounted} from 'vue';
import{defineAsyncComponent} from "vue";
import EndPoints from "@/service/EndPoints";
import {handleError} from "@/components/composable/handleError";
import{FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
const LoaderComponentTwo=defineAsyncComponent(()=>import('@/components/composable/LoaderComponentTwo'))
const ReportImage=defineAsyncComponent(()=>import('@/components/main/worldCup/ReportImage'))
export default {
  name: "PictureBase",
  components: {ReportImage, LoaderComponentTwo, FontAwesomeIcon},
  setup(){
    const list=reactive([])
    const sum=onMounted(async ()=>{
     await EndPoints.getTractor()
      .then((response)=>{
        if(response.data.success){
          list.push(response.data.data[5])
        }
      })
      .catch((error)=>{
        handleError(error)
      })

    })
    return{
      list,
      sum,
    }
  }
}
</script>

<style scoped>
.text{
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: normal;
}
</style>